<template>
  <div>
    <Head title="结算中心"></Head>
    <van-cell-group>
      <van-cell title="选择地址" is-link  to="chooseads"/>
      <van-cell :title="nowAds.name" :value="nowAds.tel" :label="nowAds.address" />
    </van-cell-group>

    <van-card
      v-for="(good,i) in orderList"
      :key="i"
      :num="good.count"
      :price="good.goodPrice"
      :title="good.goodName"
      :thumb="good.img"
    />
    <van-submit-bar :price="price*100" button-text="提交订单" />
  </div>
</template>

<script>
import Head from "@/components/Head.vue";
import { mapState } from "vuex";
export default {
  components: {
    Head
  },
  computed: {
    ...mapState(["orderList","nowAds"])
  },
  data() {
    return {
      goodsInfo: {},
      price: 0,
    };
  },
  mounted() {
    console.log(this.nowAds)
    //全局监听，页面刷新的时候将store里state的值存到sessionStorage中，然后从sessionStorage中获取，再赋值给store。然后再把session里面存的删除即可，相当于中间件的作用。
    //在页面加载时读取sessionStorage里的状态信息
    if (sessionStorage.getItem("store")) {
      this.$store.replaceState(
        Object.assign(
          {},
          this.$store.state,
          JSON.parse(sessionStorage.getItem("store"))
        )
      );
      sessionStorage.removeItem("store");
    }

    //在页面刷新时将vuex里的信息保存到sessionStorage里
    window.addEventListener("beforeunload", () => {
      sessionStorage.setItem("store", JSON.stringify(this.$store.state));
    });
    this.orderList.forEach(element => {
      this.price += element.goodPrice * element.count;
    });
   
  }
};
</script>

