<template>
  <van-nav-bar
    :title="title"
    right-text="首页"
    left-arrow
    @click-left="onClickLeft"
    @click-right="onClickRight"
  />
</template>

<script>
export default {
    props:["title"],
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    onClickRight() {
      this.$router.push({ name: "home" });
    }
  }
};
</script>

